@font-face {
    font-family: babeReg;
    src: url('../css/bebas_neue/BebasNeue Regular.otf');
}
@font-face {
    font-family: ams;
    src: url('./AmsterdamTangram.ttf');
}

html, body {
    height: 100%;
    margin: 0;

    -webkit-box-sizing: border-box;

            box-sizing: border-box;

    overflow: hidden;
}

body > * {
    height: 100%;
}

.wrap-home {
    height: 100%;

    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: repeat(10, 1fr);
    background: url('./college.JPG');
    background-size: cover;
}

.wrap-nav {
    grid-column: 1 / -1;
    grid-row: 1 / 4;

    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: repeat(2, 1fr);
}
.wrap-content {
    grid-column: 1 / -1;
    grid-row: 4 / -1;

    background: -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(transparent), color-stop(transparent), to(rgba(31, 14, 14, 0.481)));

    background: -webkit-linear-gradient(transparent, transparent, transparent, rgba(31, 14, 14, 0.481));

    background: -o-linear-gradient(transparent, transparent, transparent, rgba(31, 14, 14, 0.481));

    background: linear-gradient(transparent, transparent, transparent, rgba(31, 14, 14, 0.481));

    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: repeat(10, 1fr);
}

.wrap-content > h1 {
    grid-column: 3 / -3;
    grid-row: 5 / 6;

    -ms-flex-item-align: center;

        align-self: center;
    justify-self: center;

    font-family: ams;
    font-size: 8vh;
    color: rgb(213, 211, 211);

    -webkit-transition: text-shadow .3s;

    -o-transition: text-shadow .3s;

    transition: text-shadow .3s;
}
.wrap-content > h1:hover {
    text-shadow: black 0 0 10px;

    -webkit-transition: text-shadow .3s;

    -o-transition: text-shadow .3s;

    transition: text-shadow .3s;
}
.igIM {
    cursor: pointer;

    height: 29px;

    -webkit-transition: all 0.1s ease-out;

    -o-transition: all 0.1s ease-out;

    transition: all 0.1s ease-out; 
}
.igIM:hover {
    height: 35px;

    -webkit-transition: all 0.1s ease-in;

    -o-transition: all 0.1s ease-in;

    transition: all 0.1s ease-in;
}

.something {
    grid-column: 1 / -1;
    grid-row: 1 / -1;

    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: repeat(10, 1fr);

    background-color: white;
}
.logo {
    grid-column: 1 / -1;
    grid-row: 1 / 3;
    height: 27vh;
    justify-self: center;

    border: 0;
    -webkit-box-shadow: gainsboro 0 0 100px 20px;
            box-shadow: gainsboro 0 0 100px 20px;
    border-radius: 100%;
}
.follow-text {
    grid-column: 2 / -2;
    grid-row: 6 / 7;

    -ms-flex-item-align: center;

        align-self: center;
    justify-self: center;

    font-family: babeReg;
    font-size: 4vh;
    color: rgb(75, 68, 68);
}
.social-media {
    grid-column: 5 / 7;
    grid-row: 7 / 8;

    -ms-flex-align: center;

        align-items: center;
    justify-items: center;

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
}
.team-axiom {
    grid-column: -3 / -1;
    grid-row: -2 / -1;

    -ms-flex-item-align: center;

        align-self: center;
    justify-self: center;

    font-family: babeReg;
    font-size: 2vh;
}

@media only screen and (max-width: 600px) {
    .social-media {
        grid-column: 4 / 8;
    }
    .igIM {
        height: 20px;
    }
}



